.no-scroll {
  overflow: hidden; }

:root {
  --basic-black: #000000;
  --basic-blue: #00b2ff;
  --basic-white: #ffffff;
  --logo-black: #203c42;
  --logo-grey: #788891;
  --special-black: #1b2d37;
  --text-grey: #666666; }

/* pt-mono-regular - latin_cyrillic */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: local("PT Mono"), url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff"); }

/* roboto-condensed-regular - latin_cyrillic */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

/* roboto-condensed-700 - latin_cyrillic */
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-700.woff2") format("woff2"), url("../fonts/roboto-condensed-700.woff") format("woff"); }

/* roboto-500 - latin_cyrillic */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Roboto"), url("../fonts/roboto-500.woff2") format("woff2"), url("../fonts/roboto-500.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  font-style: normal;
  font-family: "PT Mono", "Arial", sans-serif;
  color: var(--text-grey);
  background-color: var(--basic-white); }

.page body {
  position: relative;
  min-width: 320px; }

.header {
  padding: 0 Max(15px, calc(50% - 705px));
  padding-top: 33px;
  padding-bottom: 90px;
  color: var(--basic-white);
  background-color: var(--special-black);
  background-image: url("../img/main-background.png"), url("../img/pcb.png");
  background-repeat: no-repeat;
  background-position: 50% 50%, top 257px left 85%;
  background-size: cover, 36.7% auto; }
  @media (min-resolution: 2dppx) {
    .header {
      background-image: url("../img/main-background@2x.png"), url("../img/pcb@2x.png"); } }
  @media only screen and (max-width: 1023px) {
    .header {
      padding: 20px 25px 163px;
      background-position: 50% 50%, top 117px right -200px;
      background-size: cover, 59.7% auto; } }
  @media only screen and (max-width: 767px) {
    .header {
      padding: 15px 15px 70px;
      background-position: 50% 50%, 140% 22%;
      background-size: cover, 95% auto; } }

.header__navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 230px; }
  @media only screen and (max-width: 1023px) {
    .header__navigation {
      margin-bottom: 115px; } }
  @media only screen and (max-width: 767px) {
    .header__navigation {
      margin-bottom: 233px; } }

.header__contact {
  display: flex;
  align-items: center;
  margin: 0 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px; }
  .header__contact:hover, .header__contact:focus {
    opacity: 0.7; }
  .header__contact:active {
    opacity: 0.5; }
  @media only screen and (max-width: 1023px) {
    .header__contact {
      margin: 0;
      margin-left: 40px;
      transform: scale(1.3); } }
  @media only screen and (max-width: 767px) {
    .header__contact {
      margin-left: 35px; } }

.header__contact svg {
  margin-right: 16px; }
  @media only screen and (max-width: 1023px) {
    .header__contact svg {
      margin-right: 0; } }

.header__contact:nth-of-type(2n) {
  margin-right: 70px; }
  @media only screen and (max-width: 1023px) {
    .header__contact:nth-of-type(2n) {
      margin-right: 0; } }

@media only screen and (max-width: 1023px) {
  .header__contact span {
    display: none; } }

.header__button {
  display: block;
  min-width: 180px;
  margin-left: 30px;
  padding: 15px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid var(--basic-white);
  border-radius: 5px; }
  .header__button:hover, .header__button:focus {
    opacity: 0.7; }
  .header__button:active {
    opacity: 0.5; }
  @media only screen and (max-width: 1023px) {
    .header__button {
      display: none; } }

.header__intro {
  margin-bottom: 95px; }
  @media only screen and (max-width: 1023px) {
    .header__intro {
      margin-bottom: unset; } }

.header__scroll {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  transform: rotate(180deg);
  opacity: 0.5;
  writing-mode: vertical-lr; }
  .header__scroll:hover, .header__scroll:focus {
    opacity: 1; }
  .header__scroll:active {
    opacity: 0.7; }
  @media only screen and (max-width: 1023px) {
    .header__scroll {
      display: none; } }

.logo {
  display: grid;
  margin-right: auto;
  padding-right: 15px;
  grid-gap: 15px;
  grid-template-columns: auto auto; }

@media only screen and (max-width: 767px) {
  .logo img {
    width: 38px; } }

.logo p {
  display: grid;
  margin: 0;
  padding-top: 5px;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: var(--basic-white);
  text-transform: capitalize; }
  @media only screen and (max-width: 767px) {
    .logo p {
      display: none; } }

.logo span {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  font-family: "Roboto", "Arial", sans-serif;
  color: var(--logo-grey);
  text-transform: none; }

.intro {
  max-width: 52%;
  margin-left: -5px; }
  @media only screen and (max-width: 1023px) {
    .intro {
      max-width: unset;
      margin-left: 0;
      padding-right: 225px;
      padding-left: 62px; } }
  @media only screen and (max-width: 767px) {
    .intro {
      padding: 0; } }

.intro h1 {
  margin: 0;
  margin-bottom: 27px;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.07em; }
  @media only screen and (max-width: 1023px) {
    .intro h1 {
      margin-bottom: 30px;
      font-size: 60px;
      line-height: 70px; } }
  @media only screen and (max-width: 767px) {
    .intro h1 {
      margin-bottom: 27px;
      font-size: 40px;
      line-height: 47px; } }

.intro p {
  position: relative;
  margin: 0;
  margin-bottom: 45px;
  margin-left: 97px;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px; }
  @media only screen and (max-width: 1023px) {
    .intro p {
      margin-bottom: 55px;
      margin-left: 50px;
      font-size: 18px;
      line-height: 28px; } }
  @media only screen and (max-width: 767px) {
    .intro p {
      margin-bottom: 40px;
      font-size: 16px;
      line-height: 25px; } }

.intro p::before {
  content: "";
  position: absolute;
  top: 16px;
  left: -95px;
  width: 66px;
  height: 2px;
  background-color: var(--basic-white); }
  @media only screen and (max-width: 1023px) {
    .intro p::before {
      left: -47px;
      width: 33px; } }

.intro__link {
  display: block;
  max-width: 407px;
  margin-left: 97px;
  padding: 17px 20px 18px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: var(--basic-blue);
  border-radius: 5px; }
  .intro__link:hover, .intro__link:focus {
    opacity: 0.7; }
  .intro__link:active {
    opacity: 0.5; }
  @media only screen and (max-width: 1023px) {
    .intro__link {
      margin-left: 50px; } }
  @media only screen and (max-width: 767px) {
    .intro__link {
      max-width: unset;
      margin-left: 0; } }

@media only screen and (max-width: 767px) {
  .intro__link span:first-child {
    display: none; } }

.intro__link span:last-child {
  display: none; }
  @media only screen and (max-width: 767px) {
    .intro__link span:last-child {
      display: inline; } }
